import { createPlugin, createRoutableExtension, createComponentExtension } from '@backstage/core-plugin-api';

import { rootRouteRef } from './routes';

export const scoreCardPlugin = createPlugin({
  id: 'score-card',
  routes: {
    root: rootRouteRef,
  },
});

export const ScoreCardPage = scoreCardPlugin.provide(
  createRoutableExtension({
    component: () =>
      import('./components/ScoreCardPage').then(m => m.ScoreCardPage),
    mountPoint: rootRouteRef,
  }),
);

export const EntityScoreCardContent = scoreCardPlugin.provide(
  createComponentExtension({
    component: {
      lazy: () =>
        import('./components/ScoreCard').then(m => m.ScoreCard),
    },
  }),
);
